  .define-level-left-block {
    max-width: 100%;
    text-align: center;
  }
}

@media (max-width: 995px) {
  .define-level-right-block {
    display: none;
  }
}

.define-level__check-level {
  margin: 0;
}

@media (max-width: 995px) {
  .define-level__check-level {
    margin: 0px auto;
  }
}

.footer {
  background: #0b1754;
}

.footer-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;
}

@media (max-width: 968px) {
  .footer-row {
    display: block;
    text-align: center;
    padding: 30px 0;
  }
}

.footer-logo h3 {
  text-transform: uppercase;
  text-align: center;
  font-weight: 800;
  font-size: 25px;
}

.footer-logo span {
  font-weight: 400;
  font-size: 15px;
}

@media (max-width: 968px) {
  .footer-logo {
    margin-bottom: 15px;
  }
}

.footer-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  max-width: 470px;
  width: 100%;
}

@media (max-width: 968px) {
  .footer-text {
    max-width: 100%;
    margin-bottom: 20px;
  }
}

.footer-links li:not(:last-child) {
  margin-bottom: 30px;
}

.footer-links li a {
  color: #fff;
  font-weight: 400;
  font-size: 15px;
  position: relative;
}

.footer-links li a:hover {
  text-decoration: underline;
}

.copyright {
  text-align: center;
  font-weight: 400;
  font-size: 15px;
  color: #fff;
  background: #008de6;
  padding: 20px 0;
}
